import React from "react";
import {
  Col,
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  ModalBody,
  ModalHeader,
  ModalFooter
} from "reactstrap";

const QuestionForm = ({ handleSubmit, handleChange, config }) => {
  return (
    <div style={{ backgroundColor: "white", color: "grey" }}>
      <ModalHeader>Ask a Question</ModalHeader>
      <ModalBody>
        <Form onSubmit={handleSubmit}>
          <FormGroup row>
            <Label sm={2}>Question: </Label>
            <Col sm={10}>
              <Input type="textarea" onChange={handleChange} name="question" />
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label sm={2}>Answers: </Label>
            <Col sm={5}>
              <Input
                type="text"
                onChange={handleChange}
                name="answer1"
                placeholder="Answer 1"
              />
            </Col>
            <Col sm={5}>
              <Input
                type="text"
                onChange={handleChange}
                name="answer2"
                placeholder="Answer 2"
              />
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label sm={2}>Bounty: </Label>
            <Col sm={10}>
              <Input type="text" onChange={handleChange} name="bounty" />
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label sm={3}>How man can answer?: </Label>
            <Col sm={10}>
              <Input type="text" onChange={handleChange} name="answers" placeholder="3"  />
              <em>Min: <strong>{config.minAnswers}</strong> Max: <strong>{config.maxAnswers}</strong></em>
            </Col>
          </FormGroup>
        </Form>
      </ModalBody>
      <ModalFooter>
        <Button onClick={handleSubmit}>Submit</Button>
      </ModalFooter>
    </div>
  );
};

export default QuestionForm;



