import React, { Component } from "react";
import { Button, Container, Col, Row, Modal } from "reactstrap";
import Nav from "./Nav";
import { REFRESH_INTERVAL } from "../constants/Constants";
import QuestionList from "./QuestionList";
import QuestionForm from "../containers/QuestionForm";
import QuestionMain from "./QuestionMain";

class App extends Component {
  constructor(props) {
    super(props);

    this.props.initQuestionCoopContract(this.props.eth);
    this.props.appLoop(this.props.eth);
    setInterval(() => {
      this.props.appLoop(this.props.eth);
    }, REFRESH_INTERVAL);

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.toggle = this.toggle.bind(this);
  }

  toggle() {
    this.props.toggleQuestionModal();
  }

  handleSubmit(event) {
    if (parseInt(this.state["answers"], 10) < this.props.config.minAnswers) {
      alert(
        "You must specify at least " +
          this.props.config.minAnswers +
          " answers!"
      );
      return;
    }

    if (parseInt(this.state["answers"], 10) > this.props.config.maxAnswers) {
      alert(
        "You must specify no more than " +
          this.props.config.minAnswers +
          " answers!"
      );
      return;
    }

    this.props.askQuestion(
      this.props.eth,
      this.state["question"],
      this.state["answer1"],
      this.state["answer2"],
      this.state["answers"],
      parseFloat(this.state["bounty"], 10)
    );
    event.preventDefault();
  }

  handleChange(event) {
    const target = event.target;
    this.setState({
      [target.name]: target.type === "checkbox" ? target.checked : target.value
    });
  }

  render() {
    return (
      <Container fluid>
        <Nav />
        <Row>
          <Col sm="3" className="NextQuestion pt-4">
            <QuestionList title={"Up Next"} questions={this.props.queue} />
          </Col>
          <Col sm="6" className="MainQuestion pt-4 text-center">
            <QuestionMain
              active={this.props.active}
              eth={this.props.eth}
              answerQuestion={this.props.answerQuestion}
            />
          </Col>
          <Col sm="3" className="sideBar pt-3">
            <div className="text-center pb-4">
              <Button onClick={this.toggle} className="btnAsk" size="lg">
                Ask a Question
              </Button>
            </div>
            <Modal isOpen={this.props.modal} toggle={this.toggle}>
              <QuestionForm
                handleChange={this.handleChange}
                handleSubmit={this.handleSubmit}
              />
            </Modal>
            <QuestionList
              title={"Your Questions"}
              questions={this.props.askedQuestions}
            />
            <QuestionList
              title={"Answered Questions"}
              questions={this.props.answeredQuestions}
            />
          </Col>
        </Row>
      </Container>
    );
  }
}

export default App;



