import { CONFIG_LOADED } from '../constants/ActionTypes';
const initialState = {};

const config = (state = initialState, action) => {
  switch(action.type) {
    case CONFIG_LOADED:
      return {
        ...state,
        ...action.config,
      };
    default:
      return state;
  }
}

export default config;



